'use client'

import { useStore } from '@/store/useStore'
import { useEffect, useState } from 'react'

// Sample menu data
const sampleMenu = [
    {
        id: '1',
        name: '아메리카노',
        price: 4000,
        category: 'drink',
        image: '/images/americano.jpg',
    },
    {
        id: '2',
        name: '카페라떼',
        price: 4500,
        category: 'drink',
        image: '/images/latte.jpg',
    },
    {
        id: '3',
        name: '카푸치노',
        price: 4500,
        category: 'drink',
        image: '/images/cappuccino.jpg',
    },
    {
        id: '4',
        name: '샌드위치',
        price: 6000,
        category: 'food',
        image: '/images/sandwich.jpg',
    },
    {
        id: '5',
        name: '케이크',
        price: 5000,
        category: 'food',
        image: '/images/cake.jpg',
    },
]

const categories = [
    { id: 'COFFEE', name: '커피' },
    { id: 'LATTE', name: '라떼' },
    { id: 'JUICE', name: '주스' },
    { id: 'ADE', name: '에이드' },
    { id: 'TEA', name: '차' },
    { id: 'SMOOTHIE', name: '스무디' },
    { id: 'FRAPE', name: '프라페' },
    { id: 'DESSERT', name: '디저트' },
]

export default function MenuPage() {
    const { menu, setMenu, addToCart } = useStore()
    const [selectedCategory, setSelectedCategory] = useState<string>('COFFEE')

    useEffect(() => {
        // Set sample menu data
        setMenu(sampleMenu)
    }, [setMenu])

    const filteredMenu = menu.filter((item) => item.category === selectedCategory)

    return (
        <div className="bg-white">
            <div className="mx-auto max-w-2xl px-4 py-16 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
                <h2 className="text-2xl font-bold tracking-tight text-gray-900">메뉴</h2>

                {/* Category tabs */}
                <div className="mt-6 border-b border-gray-200">
                    <nav className="-mb-px flex space-x-8" aria-label="Tabs">
                        {categories.map((category) => (
                            <button
                                key={category.id}
                                onClick={() => setSelectedCategory(category.id)}
                                className={`${selectedCategory === category.id
                                        ? 'border-indigo-500 text-indigo-600'
                                        : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                                    } whitespace-nowrap border-b-2 py-4 px-1 text-sm font-medium`}
                            >
                                {category.name}
                            </button>
                        ))}
                    </nav>
                </div>

                <div className="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                    {filteredMenu.map((item) => (
                        <div key={item.id} className="group relative">
                            <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md bg-gray-200 lg:aspect-none group-hover:opacity-75 lg:h-80">
                                <img
                                    src={item.image}
                                    alt={item.name}
                                    className="h-full w-full object-cover object-center lg:h-full lg:w-full"
                                />
                            </div>
                            <div className="mt-4 flex justify-between">
                                <div>
                                    <h3 className="text-sm text-gray-700">
                                        <a href="#">
                                            <span aria-hidden="true" className="absolute inset-0" />
                                            {item.name}
                                        </a>
                                    </h3>
                                    <p className="mt-1 text-sm text-gray-500">
                                        {categories.find((c) => c.id === item.category)?.name}
                                    </p>
                                </div>
                                <p className="text-sm font-medium text-gray-900">
                                    {item.price.toLocaleString()}원
                                </p>
                            </div>
                            <button
                                onClick={() => addToCart(item)}
                                className="mt-4 w-full rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                            >
                                장바구니에 담기
                            </button>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
} 